              <el-dialog
                width="30%"
                title="这是一个伪支付码"
                :visible.sync="innerVisible"
                append-to-body
                ><div>
                  <img src="../assets/love-1.gif" alt="" />
                  <img src="../assets/love-1.gif" alt="" />
                  <img src="../assets/love-1.gif" alt="" />
                  <img src="../assets/love-1.gif" alt="" />
                  <span>小心心送给你</span>
                </div>
              </el-dialog>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="innerVisible = true"
                  >购 买</el-button
                >
