        <br /><br />
        <p class="edp">
          对于流浪猫狗应当保持一定的警戒心，在拥有爱护之心的同时，应该预防流浪猫狗身上存在的
          病菌，病毒等，防止被抓伤，处理方式，如绝育加送养，或者送往当地流浪动物救助中心
        </p>
        <br />
        <p>
          联系我们，<router-link to="homepage/relation">提交</router-link
          >您的信息
        </p>
        <br />
        <p>联系电话：1000000000</p>
      </div>
    </el-drawer>
  </div>
</template>

<script>
export default {
  name: 'Index',
  data () {
    return {
      msg: '流浪猫狗救助站',
      isCollapse: true,
      drawer: false,
      show: true,
      card: false,
      dialogVisible: false,
      innerVisible: false,
      num: 1,
      items: [
        { id: 1, url: require("../assets/cat-1.jpg") },
        { id: 2, url: require("../assets/cat-2.jpg") },
        { id: 3, url: require("../assets/cat-3.jpg") },
        { id: 4, url: require("../assets/snowflakes-5.jpg") },
        { id: 5, url: require("../assets/kitty-5.jpg") },
        { id: 6, url: require("../assets/husky-6.jpg") },
      ],
      newDate: new Date(),
    }
  },
  methods: {
    homepage () {
      this.$router.push('/homepage')
    },
    handleOpen (key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath);
    },
    dateFormat () {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1
      var day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate()
      var hours = date.getHours() < 10 ? '0' + date.getHours() : date.getHours()
      var minutes = date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
      var seconds = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let week = date.getDay() // 星期
      let weekArr = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
      // 拼接 时间格式处理
      return year + '年' + month + '月' + day + '日 ' + hours + ':' + minutes + ':' + seconds + ' ' + weekArr[week]
    },
    handleClose (done) {
      this.$confirm('确认取消？')
        .then(_ => {
          done();
        })
        .catch(_ => { });
    },
    handleChange (value) {
      console.log(value);
    },
  },
  beforeCreate () {
  },
  // 挂载时间
  mounted () {
    let that = this
    this.timer = setInterval(function () {
      that.newDate = new Date().toLocaleString()
    })
  },
  // 销毁时清除计时器
  beforeDestroy: function () {
    if (this.timer) {
      clearInterval(this.timer)
    }
  },
}
</script>

<!-- 样式 -->
<style scoped>
.hello {
  display: inline;
  width: 100%;
  height: 100%;
}

.box1 {
  text-align: left;
}
.box1 img {
  position: relative;
  top: 7px;
}

.box2 {
  text-align: center;
}

h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
/* 设置底部栏位置 */
footer {
  position: relative;
  bottom: 0;
  font-family: "Courier New", Courier, monospace;
}
.edp {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1rem;
  text-indent: 2em;
  line-height: 20px;
}
.el-menu-vertical-demo:not(.el-menu--collapse) {
  width: 200px;
  min-height: 400px;
}

p {
  text-indent: 2em;
}

.demo-image__lazy .el-image {
  display: block;
  min-height: 200px;
  margin-bottom: 10px;
}

body {
  margin: 0;
  padding: 0;
}
</style>



