<template>
  <div>
    <el-row>
      <el-col :span="4">
        <div class="box">
          Process
          <h2>基本资料：</h2>
          <p>姓名：{{ fo.name }}</p>
          <p>角色：{{ fo.js }}</p>
          <p>UID：{{ fo.uid }}</p>
          <p>性别：{{ fo.sex }}</p>
          <p>生日：{{ fo.bir }}</p>
          <el-button-group
            ><el-button type="primary">确认</el-button>
            <el-button @click="dialogVisible = true">修改</el-button>
          </el-button-group>
          <el-dialog
            title="请按顺序填写："
            :visible.sync="dialogVisible"
            width="30%"
            :before-close="handleClose"
            ><div>
              <el-input v-model="fo.name"></el-input>
              <el-input v-model="fo.js"></el-input>
              <el-input v-model="fo.uid"></el-input>
              <el-input v-model="fo.sex"></el-input>
              <el-input v-model="fo.bir"></el-input>
            </div>
            <span slot="footer" class="dialog-footer">
              <el-button @click="dialogVisible = false">取 消</el-button>
              <el-button type="primary" @click="dialogVisible = false"
                >确 定</el-button
              >
            </span>
          </el-dialog>
        </div>
      </el-col>
      <el-col :span="18">
        <div class="box2">
          <h2>发布领养信息</h2>
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="宠物名称">
              <el-input v-model="form.name"></el-input>
            </el-form-item>
            <el-form-item label="地区省份">
              <el-select v-model="form.region" placeholder="请选择地区省份">
                <el-option label="河北省" value="shanghai"></el-option>
                <el-option label="天津市" value="beijing"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="寄送时间">
              <el-col :span="11">
                <el-date-picker
                  type="date"
                  placeholder="选择日期"
                  v-model="form.date1"
                  style="width: 100%"
                ></el-date-picker>
              </el-col>
              <el-col class="line" :span="2">-</el-col>
              <el-col :span="11">
                <el-time-picker
                  placeholder="选择时间"
                  v-model="form.date2"
                  style="width: 100%"
                ></el-time-picker>
              </el-col>
            </el-form-item>
            <el-form-item label="寄送小屋">
              <el-switch v-model="form.delivery"></el-switch>
            </el-form-item>
            <el-form-item label="宠物图片">
              <el-upload
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload"
              >
                <img v-if="imageUrl" :src="imageUrl" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="宠物详情">
              <el-input type="textarea" v-model="form.desc"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="onSubmit">立即发布</el-button>
              <el-button>取消</el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Process',
  data () {
    return {
      form: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      dialogVisible: false,
      imageUrl: '',
      fo: {
        name: 'Admin',
        js: '个人',
        uid: '1000',
        sex: 'SEX',
        bir: '2000'
      }
    }
  },
  methods: {
    onSubmit () {
      console.log('submit!');
    },
    handleClose (done) {
      this.$confirm('确认关闭？')
        .then(_ => {
          done();
        })
        .catch(_ => { });
    },
    handleAvatarSuccess (res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!');
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!');
      }
      return isJPG && isLt2M;
    }
  }
}
</script>

<style scoped>
.box {
  margin-left: 20px;
}
.box2 {
  margin: 5%;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>


