<template>
  <div class="box">
    <!-- 导航栏 -->
    <el-menu
      :default-active="activeIndex"
      mode="horizontal"
      @select="handleSelect"
      active-text-color="#409EFF"
      router
      background-color="#545c64"
      text-color="#fff"
    >
      <el-menu-item index="/homepage/main">
        <i class="el-icon-house"></i>主页
      </el-menu-item>
      <el-menu-item index="/homepage/knowledge">
        <i class="el-icon-chat-line-round"></i>知识中心
      </el-menu-item>
      <el-menu-item index="/homepage/process">
        <i class="el-icon-edit-outline"></i>发布领养
      </el-menu-item>
      <el-menu-item index="/homepage/relation">
        <i class="el-icon-postcard"></i>领养申请
      </el-menu-item>
      <el-menu-item index="/homepage/game">
        <i class="el-icon-mouse"></i>趣味游戏
      </el-menu-item>
      <el-menu-item index="/">
        <i class="el-icon-switch-button"></i>返回首页
      </el-menu-item>
      <el-menu-item>
        <el-button type="text" @click="dialogVisible = true">
          <i class="el-icon-user-solid"></i>
          {{ form.name }}
        </el-button>
      </el-menu-item>
    </el-menu>
    <div>
      <el-dialog
        title="输入框"
        :visible.sync="dialogVisible"
        width="30%"
        class="box"
      >
        <el-form ref="form" :model="form" label-width="80px">
          <span>请写下您的姓名：</span>
          <el-input v-model="form.name"></el-input>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false"
            >修 改</el-button
          >
        </span>
      </el-dialog>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  name: "Homepage",
  data () {
    return {
      activeIndex: '/homepage/main',
      activeIndex2: '1',
      dialogVisible: false,
      form: {
        name: 'Admin',
      }
    };
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style scoped>
body {
  margin: 0;
  padding: 0;
}

.box {
  display: inline;
}
</style>


